"use client";

import { useForm } from "react-hook-form";
import { z } from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { useState } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { TextInput, PasswordInput, Button, Paper, Title, Stack, Text, Anchor, Group, Container } from "@mantine/core";
import axios from "axios";

const schema = z.object({
  username: z.string().min(1, "Username wajib diisi"),
  password: z.string().min(1, "Password wajib diisi"),
});
type FormValues = z.infer<typeof schema>;

export default function LoginPage() {
  const router = useRouter();
  const params = useSearchParams();
  const [error, setError] = useState<string | null>(null);

  const { register, handleSubmit, formState: { errors, isSubmitting } } = useForm<FormValues>({
    resolver: zodResolver(schema),
    defaultValues: { username: "", password: "" },
  });

  const onSubmit = async (values: FormValues) => {
    setError(null);
    try {
      const res = await axios.post("/api/auth/login", values);
      if (res.data?.success) {
        const next = params.get("redirect") || "/cases";
        router.replace(next);
      } else {
        setError("Login gagal");
      }
    } catch (e: any) {
      setError(e?.response?.data?.message || "Login gagal");
    }
  };

  return (
    <Container size={420} py="xl">
      <Title order={2} ta="center">Masuk</Title>
      <Text c="dimmed" size="sm" ta="center" mt="xs">
        Demo user: <b>admin/admin123</b> atau <b>leo/indotruck</b>
      </Text>

      <Paper withBorder shadow="sm" p="lg" mt="md" radius="md">
        <form onSubmit={handleSubmit(onSubmit)}>
          <Stack>
            <TextInput
              label="Username"
              placeholder="username"
              {...register("username")}
              error={errors.username?.message}
              autoComplete="username"
            />
            <PasswordInput
              label="Password"
              placeholder="••••••••"
              {...register("password")}
              error={errors.password?.message}
              autoComplete="current-password"
            />
            {error && <Text c="red">{error}</Text>}
            <Button type="submit" loading={isSubmitting}>Masuk</Button>
          </Stack>
        </form>
        <Group justify="apart" mt="md">
          <Anchor size="sm" c="dimmed" href="/">Kembali</Anchor>
        </Group>
      </Paper>
    </Container>
  );
}
